//taking on Array out of Dictionary(Ojects)
var emojisWEKnow = Object.keys(emojiDictionary);

// console.log(emojisWEKnow);

//*********/ Main app /*********//

export default function App() {
  //two states meaning=Do we know the meaning?  & setMeaning= which sets the meaning
  const [meaning, setMeaning] = useState("");

  // activates when we give an input
  function emojiinputHandler(event) {
    //processing
    // console.log(event.target.value);

    var userInput = event.target.value;

    var meaning = emojiDictionary[userInput];

    if (meaning === undefined) {
      meaning = "We do not have this in our data base";
    }

    setMeaning(meaning); //React call to show output
  }
  // activates when we click on dispayed keys of an object using array
  function emojiClickHandler(emo) {
    //processing
    // console.log(emo);
    var meaning = emojiDictionary[emo];

    setMeaning(meaning); //React call to show output
  }

  return (
    <div className="App">
      <h1>Bon Appetit ğŸ›</h1>

      <input onChange={emojiinputHandler}></input>

      <div className="output"> {meaning} </div>
      {/* {actual output set by React using use state} */}

      <h3> Emojis We Know </h3>

      {emojisWEKnow.map(function (emo) {
        return (
          <span
            onClick={() => emojiClickHandler(emo)}
            key={emo}
            style={{ fontSize: "2rem", padding: "0.2rem", cursor: "pointer" }}
          >
            {emo}
          </span>
        );
      })}

      <div className="pink">
        <a href="https://docs.google.com/spreadsheets/d/1qrhOG0vppK_4GFbPanZS-BXv7JPoFGCpAWUqvffgXcg/edit#gid=0">
          ***create dictionary objects
        </a>
      </div>
    </div>
  );
}
